.workMobile {
	padding: 4rem 1rem;
	&-heading {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: flex-start;
		text-align: center;
		height: 100vh;
		max-width: 280px;
		gap: 12px;
		margin: auto;
		position: sticky;
		top: 4rem;
		// margin-bottom: 80vh;
	}
}

//* CARD MOBILE *//
.cardContainerMobile {
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	position: sticky;
	top: 4rem;
}
.cardMobile {
	width: 100%;
	height: 100%;
	border-radius: 24px;
	border: 1px solid rgba(230, 230, 230, 0.12);
	background: radial-gradient(50% 50% at 50% 50%, #130d31 0%, #050519 100%);
	box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.25);
	overflow: hidden;

	&-border {
		position: relative;
		display: flex;
		width: 328px;
		height: 420px;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		border-radius: 24px;
		border: 1px solid rgba(230, 230, 230, 0.12);
	}
	&-img {
		width: 100%;
		height: 100%;
	}
	&-info {
		position: absolute;
		bottom: 0;
		display: flex;
		width: 100%;
		padding: 24px;
		flex-direction: column;
		align-items: flex-start;
		background: radial-gradient(50% 50% at 50% 50%, rgba(19, 13, 49, 0.01) 0%, rgba(5, 5, 25, 0.01) 100%);
		backdrop-filter: blur(24px);
		&-title {
			font-size: 16px;
			font-style: normal;
			font-weight: 700;
			line-height: 1.25;
			color: var(--text-black);
			margin-bottom: 12px;
		}
		&-text{
			font-size: 14px;
			font-style: normal;
			font-weight: 500;
			line-height: 1.5;
			color: var(--text-black);
			margin-bottom: 24px;
		}
	}
	&-content {
		position: relative;
		width: 100%;
		height: 100%;
	}
	&-shadow {
		position: absolute;
		width: 100%;
		height: 100%;
		border-radius: 24px;
		border: 1px solid rgba(230, 230, 230, 0.24);
		box-shadow: inset 0px 0px 20px 20px rgba(0, 0, 0, 0.25);
	}
}
