.aboutCard {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	align-self: stretch;
	border-radius: 1.5rem;
	border: 1px solid rgba(230, 230, 230, 0.12);
	box-shadow: 0px 0px 16px 4px rgba(0, 0, 0, 0.25);
	padding: 2rem;
	overflow: hidden;

	&-border {
		background-color: rgba(230, 230, 230, 0.12);
	}

	&-1 {
		width: 282px;
		height: 329px;
		background: radial-gradient(50% 50% at 50% 50%, #130d31 0%, #050519 100%);
		gap: 1.5rem;
		&-logo {
			margin-bottom: -32px;
			transform: translateY(-30%);
			filter: none;
			transition: filter 0.3s ease;

			&-hover {
				filter: drop-shadow(0 0 16px rgba(0, 0, 255, 0.6)) brightness(1.2) saturate(1.5);
				transition: filter 0.3s ease;
			}
		}

		&-heading {
			position: relative;
			font-size: 24px;
			font-style: normal;
			font-weight: 700;
			line-height: 1.25;
			color: var(--text-white);
			gap: 12px;
		}
		&-text {
			font-size: 18px;
			font-style: normal;
			font-weight: 500;
			line-height: 1.25;
			color: var(--text-normal);
		}
	}

	&-2 {
		position: relative;
		width: 588px;
		height: 329px;
		background: radial-gradient(50% 50% at 50% 50%, rgba(19, 13, 49, 0.9) 0%, rgba(5, 5, 25, 0.9) 100%);

		&-heading {
			font-size: 24px;
			font-style: normal;
			font-weight: 700;
			line-height: 1.25;
			color: var(--text-white);
		}
	}

	&-3 {
		position: relative;
		width: 282px;
		height: 329px;
		background: radial-gradient(50% 50% at 50% 50%, #130d31 0%, #050519 100%);
		justify-content: space-between;
		align-items: center;
		cursor: pointer;

		&-heading {
			font-size: 24px;
			font-style: normal;
			font-weight: 700;
			line-height: 1.25;
			color: var(--text-white);
			gap: 12px;
		}
		&-text {
			font-size: 18px;
			font-style: normal;
			font-weight: 500;
			line-height: 1.25;
