			height: 54px;
			flex-direction: column;
			justify-content: space-between;
			align-items: flex-end;
			flex-shrink: 0;
			align-self: stretch;
		}
		&-heading {
			font-size: 24px;
			font-style: normal;
			font-weight: 700;
			line-height: 1.25;
