	&-5 {
		position: relative;
		width: 688px;
		height: 221px;
		background: radial-gradient(50% 50% at 50% 50%, rgba(19, 13, 49, 0.9) 0%, rgba(5, 5, 25, 0.9) 100%);
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;
		// flex: 1 0 0;

		&-heading {
			font-size: 24px;
			font-style: normal;
			font-weight: 700;
			line-height: 1.25;
			color: var(--text-white);
			gap: 12px;
		}
		&-images {
			display: flex;
			justify-content: center;
			align-items: center;
			gap: -16px;
			align-self: stretch;
			// display: grid;
			// grid-auto-flow: column;
			// grid-template-columns: repeat(auto-fit, minmax(100px, 1fr)); /* Adjust based on your layout */
			// gap: -16px; /* Desired gap on hover */
			transition: gap 0.3s ease;
		}
		&-img {
			width: 128px;
			height: 85.305px;
			border-radius: 12px;
			border: 1px solid rgba(255, 255, 255, 0.12);
			box-shadow: 0px 0px 16px 4px rgba(0, 0, 0, 0.25);
			// object-fit: fit;
			transition: all 0.2s ease;
			transition: all 0.2s ease, margin 0.3s ease; // Add margin to the transition
			margin: 0; // Start with no margin for a "negative gap" effect

			&-1 {
				transform: rotate(-7.189deg);
			}
			&-2 {
				transform: rotate(13.245deg);
			}
			&-3 {
				transform: rotate(-2.383deg);
			}
			&-4 {
				transform: rotate(-11.248deg);
			}

			&-hover {
				transform: rotate(0deg);
				margin: 0 12px; // Adjust as needed to achieve the desired "gap"
				transition: all 0.2s ease;
			}
		}
	}
}

