		border: 1px solid #264ce45d;
		border-radius: 50%;
		background: linear-gradient(180deg, rgba(38, 77, 228, 0.25) 0%, rgba(103, 145, 245, 0.25) 100%);
		backdrop-filter: blur(4px);
		&:hover {
			filter: drop-shadow(0 10px 32px rgba(38, 77, 228, 1));
			transition: filter 0.3s ease;
		}
		&::after {
			content: 'CSS'; // Text to display in the speech bubble.
			position: absolute;
			top: -50%; // Position below the icon.
			left: 50%; // Center align.
			transform: translateX(-50%); // Center the speech bubble.
			width: max-content;
			padding: 4px 8px;
			border-radius: 4px;
			background-color: #264ce4;
			color: #fff;
