			line-height: 1.25;
			opacity: 0; // Initially hidden.
			visibility: hidden;
			transition: opacity 0.3s, visibility 0.3s;
			white-space: nowrap; // Prevents the text from wrapping.
		}
		// Triangle pointer
		&::before {
			content: '';
			position: absolute;
			top: -23%; // Position above the speech bubble.
