.iconPopUp {
	display: inline-flex;
	justify-content: center;
	padding: 0px 0px;
	align-items: center;
	gap: 0px;
	border-radius: 0px;
	&:hover {
		display: inline-flex;
		justify-content: center;
		padding: 4px 8px;
		align-items: center;
		gap: 10px;
		border-radius: 4px;
	}

	&-text {
		font-size: 14px;
		font-style: normal;
		font-weight: 700;
		line-height: 1.25;
		text-transform: uppercase;
		color: #ffffff;
	}

	&-css {
		position: absolute;
		top: 10%;
		left: 10%;
		transform: translate(-50%, -50%);
		background: #264de4;
		box-shadow: 0px 4px 2px 0px #264de4 inset;
		&::after {
			content: '';
			position: absolute;
			bottom: -1px;
			left: 50%;
			transform: translateX(-50%);
			width: 8px; // Match SVG width
			height: 8px; // Match SVG height
			background: white;
			transform: rotate(45deg) translateY(50%);
			z-index: -3;
			background: #264de4;
