.contact {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow-y: hidden;
    overflow-x: hidden; /* Prevent horizontal movement */
    max-width: 100vw;

	&-header {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 56px;
		margin-top: 128px;
        transform: translateY(400px);
        z-index: -1;
		&-main {
			font-size: 64px;
			font-style: normal;
			font-weight: 700;
			line-height: 1.25;
		}
	}
}

.ellipseContent {
	position: relative;
	display: flex;
	flex-direction: column;
	// justify-content: center;
	align-items: center;
	gap: 36px;
    margin-top: 3rem;

    &-center{
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        gap: 12px;
    }
	&-main {
		font-size: 24px;
		font-style: normal;
		font-weight: 700;
		line-height: 1.25;
		color: var(--text-white);
	}
	&-text {
		font-size: 18px;
		font-style: normal;
		font-weight: 500;
        line-height: 1.25;
	}
    &-small{
        font-size: 11px;
		font-style: normal;
		font-weight: 400;
        line-height: 1.25;
    }
}

.contactEllipse {
	position: relative;
	width: calc(110vw);
	height: calc(80vh);
	margin-top: -17rem;
	transform: translateY(50%);
	background: radial-gradient(40.36% 129.52% at 50% 50.52%, rgba(230, 230, 230, 0.24) 0%, rgba(0, 2, 18, 0.24) 100%);
	border: 2px solid transparent;
	border-radius: 50%;
	z-index: -1;

	&::before {
		content: '';
		position: absolute;
		width: calc(110vw); /* 2px smaller than the parent element */
		height: calc(80vh + 10px); /* 1px smaller than the parent element */
		background-color: #000212;
		border-radius: 50%;
		z-index: -2; /* Behind the parent element */
		left: 50%;
		transform: translate(-50%);
	}

	&::after {
		content: '';
		position: absolute;
		left: 50%;
		transform: translate(-50%, 50%);
		width: 100%;
		height: 500px;
		height: 81vh;
		background-color: #000212;

	}

	&-light {
		position: absolute;
		bottom: 0px;
        width: 70vw;
        height: 20vh;
		flex-shrink: 0;
		border-radius: 50%;
		background: rgba(124, 84, 247, 0.5);
		filter: blur(100px);
		z-index: -2;
	}
}


@media(max-width: 1024px){
    .contact {
        &-header {
            transform: translateY(400px);
            margin-bottom: 4rem;
            &-main {
                font-size: 32px;
            }
        }
    }
    
    .ellipseContent {
        margin-top: 3rem;
        &-center{
            gap: 12px;
        }
        &-main {
            font-size: 16px;
        }
        &-text {
            font-size: 14px;
        }
    }
    
    .contactEllipse {
        width: calc(250vw);
        height: 102vh;
        margin-top: -18rem;
		background: radial-gradient(40.36% 129.52% at 50% 50.52%, rgba(230, 230, 230, 0.24) 0%, rgba(0, 2, 18, 0.24) 100%);
    
        &::before {
            content: '';
            width: calc(250vw - 2px); /* 2px smaller than the parent element */
            height: calc(102vh - 1px); /* 1px smaller than the parent element */
        }
    
        &::after {
            content: '';
            left: 50%;
            transform: translate(-50%, 50%);
            width: 100%;
            height: 500px;
            height: 81vh;
            background-color: #000212;
        }
    
        &-light {
            bottom: 0px;
            width: 100vw;
            height: 18vh;

        }
    }
    
}