.header {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 7.5rem;
	width: 49.5rem;
	gap: 16px;
	align-self: stretch;
	margin-bottom: -30rem;

	&-pic {
		width: 5rem;
		height: 5rem;
		border-radius: 5rem;
		border: 0.0625rem solid #000212;
		background: url('../../img//profilePic/profilePic.jpeg') lightgray 50% / cover no-repeat;
		background-size: 130%;
	}

	&-welcome {
		font-size: 64px;
		font-style: normal;
		font-weight: 700;
		line-height: 1.25;
	}

	&-info {
		text-align: center;
		font-size: 2.25rem;
		font-style: normal;
		font-weight: 500;
		line-height: 1.25;
	}
}

.ellipse {
	position: relative;
	width: calc(110vw);
	height: calc(80vh);
	transform: translateY(-80%);
	background: radial-gradient(40.36% 129.52% at 50% 59.52%, rgba(230, 230, 230, 0.24) 0%, rgba(0, 2, 18, 0.24) 100%);
	border: 2px solid transparent;
	border-radius: 50%;
	z-index: -1;

	&::before {
		content: '';
		position: absolute;
		width: calc(110vw); /* 2px smaller than the parent element */
		height: calc(80vh - 5px); /* 1px smaller than the parent element */
		background-color: #000212;
		border-radius: 50%;
		z-index: -2; /* Behind the parent element */
		left: 50%;
		transform: translate(-50%);
	}

	&::after {
		content: '';
		position: absolute;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 100%;
		height: 500px;
		background-color: #000212;
	}

	&-light {
		position: absolute;
		top: 304px;
		// top:  0;
		width: 722px;
		height: 282px;
		width: 70vw;
		height: 20vh;
		flex-shrink: 0;
		border-radius: 50%;
		background: rgba(124, 84, 247, 0.5);
		// background: red;
		filter: blur(100px);
		z-index: -2;
	}
}

@media (max-width: 1024px) {
	.header {
		margin-top: 9.25rem;
		width: 100vw;
		max-width: 100vw;
		padding: 0 1rem;
		overflow-y: hidden;
		overflow-x: hidden;
		// overflow-x: hidden;
		margin-bottom: -40rem;

		&-pic {
		}

		&-welcome {
			font-size: 32px;
		}

		&-info {
			font-size: 1.125rem;
		}
	}

	.ellipse {
		// top: 9.25rem;
		width: calc(250vw);
		height: 102vh;
		transform: translateY(-90%);

		background: radial-gradient(20.36% 129.52% at 50% 50.52%, rgba(230, 230, 230, 0.24) 0%, rgba(0, 2, 18, 0.24) 100%);
		&::before {
			content: '';
			width: calc(250vw - 2px); /* 2px smaller than the parent element */
			height: calc(102vh - 4px); /* 1px smaller than the parent element */
		}

		&::after {
			height: 300px;
		}

		&-light {

			width: 322px;
			height: 182px;
			// width: 100vw;
			// height: 20vh;
			top: 10%;
		}
	}
}

