.work {
	display: flex;
	flex-direction: column;
	padding: 4rem 7.5rem;
	gap: 3rem;
	position: sticky;
	top: 1rem;

	&-section {
		position: relative;
		height: 400vh;
	}
	&-content {
		position: sticky;
		display: flex;
		flex-direction: column;
		justify-content: center;
		top: 0;
		height: 100vh;
		align-items: center;
		// overflow: hidden;
		gap: 3rem;
	}

	&-heading {
		display: flex;
		width: 384px;
		padding: 0px 16px;
		margin-top: 2rem;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 12px;
		text-align: center;
		&-main {
			font-size: 48px;
			font-style: normal;
			font-weight: 700;
			line-height: 1.25;
		}
		&-info {
			font-size: 24px;
			font-style: normal;
			font-weight: 500;
			line-height: 1.25;
		}
	}

	&-elipse {
		position: absolute;
		width: 492px;
		height: 492px;
		flex-shrink: 0;
		border-radius: 492px;
		transform: translate(-10%, 10%);
		background: rgba(180, 188, 208, 0.12);
		filter: blur(128px);
	}
}

.workCards {
	display: flex;
	position: relative;
	justify-content: center;
	align-items: center;
	gap: 97px;
	align-self: stretch;

	&-info {
		display: flex;
		position: relative;
		justify-content: center;
		flex-direction: column;
		gap: 16px;
		margin-left: 102px;
		margin-right: 0;
		width: 100%;
		height: 100%;
	}
	&-title {
		color: var(--text-white);
		font-size: 24px;
		font-style: normal;
		font-weight: 700;
		line-height: 1.25;
	}
	&-text {
		font-size: 18px;
		font-style: normal;
		font-weight: 500;
		line-height: 1.25;
	}
}
.cardInfo {
	position: absolute;
}

@media (max-width: 1024px) {
	.work {
		padding: 4rem 1rem;
		gap: 2rem;
		margin-top: 12rem;

		&-heading {
			display: flex;
			&-main {
				font-size: 24px;
			}
			&-info {
				font-size: 16px;
			}
		}
	}

	.workCards {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 97px;
		align-self: stretch;

		&-info {
			display: flex;
			flex-direction: column;
			gap: 16px;
			margin-left: 102px;
			margin-right: 0;
		}
		&-title {
			color: var(--text-white);
			font-size: 24px;
			font-style: normal;
			font-weight: 700;
			line-height: 1.25;
		}
		&-text {
			font-size: 18px;
			font-style: normal;
			font-weight: 500;
			line-height: 1.25;
		}
	}
}
